import style from './fisica.css';
import { Component } from 'preact';
import Latex from '../components/latex';
import Panel from '../components/panel';
import Split from '../components/split';
import Plus from '../components/plus';
import Minus from '../components/minus';
import Todo from '../components/todo';

const r = String.raw;

export default class Fisica extends Component {
	render() {
        return (
            <div>
                <h1>Fisica (2019)</h1>
                <h2>Vettori</h2>
                <Split>
                    <Panel>
                        <h3>
                            Componenti cartesiane
                        </h3>
                        <p>
                            Usa le regole base della trigonometria:
                        </p>
                        <p>
                            <Latex>{r`\vec{v} = \vec{v}_x + \vec{v}_y`}</Latex>
                        </p>
                        <p>
                            <Latex>{r`\left | \vec{v}_x \right | = \left | \vec{v} \right | \sin \alpha`}</Latex>
                        </p>
                        <p>
                            <Latex>{r`\left | \vec{v}_y \right | = \left | \vec{v} \right | \cos \alpha`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Somma
                        </h3>
                        <p>
                            Scomponi in componenti, poi sommali:
                        </p>
                        <p>
                            <Latex>{r`\vec{v} + \vec{w} = (\vec{v}_x + \vec{w}_x) + (\vec{v}_y + \vec{w}_y)`}</Latex>
                        </p>
                        <p>
                            Produce il vettore risultante dall'applicazione della regola del parallelogramma.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Differenza
                        </h3>
                        <p>
                            Alla fine  sempre una somma:
                        </p>
                        <p>
                            <Latex>{r`\vec{v} - \vec{w} = (\vec{v}_x - \vec{w}_x) + (\vec{v}_y - \vec{w}_y)`}</Latex>
                        </p>
                        <p>
                            Produce il vettore che parte da <Latex>w</Latex> e arriva a <Latex>v</Latex>.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Prodotto scalare
                        </h3>
                        <p>
                            Si chiama scalare perch il risultato  uno scalare, non un vettore.
                        </p>
                        <p>
                            <Latex>{r`\vec{v} \cdot \vec{w} = \left | \vec{v} \right | \left | \vec{w} \right | \cos \alpha`}</Latex>
                        </p>
                        <p>
                            Produce il modulo della proiezione di <Latex>{r`\vec{a}`}</Latex> su <Latex>{r`\vec{b}`}</Latex>.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Prodotto vettoriale
                        </h3>
                        <p>
                            Si chiama vettoriale perch il risultato  un altro vettore.
                        </p>
                        <ul>
                            <li><Latex>{r`\vec{c} = \vec{a} \times \vec{b}`}</Latex></li>
                            <li><Latex>{r`\left | \vec{c} \right | = \left | \vec{a} \right | \cdot \left | \vec{b} \right | \cdot \sin(\alpha)`}</Latex></li>
                            <li><a href="https://it.wikipedia.org/wiki/Regola_della_mano_destra">Regola della mano destra</a></li>
                        </ul>
                        <p>
                            Non  commutativo!
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Leggi di Newton
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            1: Inerzia
                        </h3>
                        <p>
                            Se un corpo puntiforme ha forza risultante nulla, allora la sua velocit non cambia.
                        </p>
                        <p>
                            <Latex>{r`\Sigma \vec{F} = 0 \Longleftrightarrow \Delta v = 0`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            2: Proporzionalit
                        </h3>
                        <p>
                            La forza risultante di un corpo  direttamente proporzionale alla sua accelerazione, e la costante di proporzionalit  la <i>massa</i>.
                        </p>
                        <p>
                            <Latex>{r`\Sigma \vec{F} = m \vec{a}`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            3: Azione e reazione
                        </h3>
                        <p>
                            Due corpi esercitano forze uguali e opposte uno sull'altro. 
                        </p>
                        <p>
                            <Latex>{r`\vec{F}_{21} = -\vec{F}_{12}`}</Latex>
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Forza di gravit
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Tra due corpi
                        </h3>
                        <p>
                            Due corpi puntiformi si attirano uno verso l'altro con forza:
                        </p>
                        <p>
                            <Latex>{r`\left | \vec{F} \right | = G \frac{m_1 m_2}{s^2}`}</Latex>
                        </p>
                        <p>
                            <Latex>G</Latex>  la <i>costante di gravitazione universale</i> e vale:
                        </p>
                        <p>
                            <Latex>{r`G = 6.67 \cdot 10^{-11} \frac{N m^2}{{kg}^2}`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Verso la Terra
                        </h3>
                        <p>
                            Se nel sistema di riferimento consideriamo la Terra ferma, allora un corpo  attratto verso la Terra con forza <i>peso</i> uguale a:
                        </p>
                        <p>
                            <Latex>{r`\left | \vec{F} \right | = g m`}</Latex>
                        </p>
                        <p>
                            <Latex>g</Latex>  la <i>costante di gravit</i> della Terra, e vale:
                        </p>
                        <p>
                            <Latex>{r`g = 9.81 \frac{m}{s^2}`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Su pianeti diversi
                        </h3>
                        <p>
                            Per pianeti diversi dalla Terra vale la stessa regola:
                        </p>
                        <p>
                            <Latex>{r`\left | \vec{F} \right | = g m`}</Latex>
                        </p>
                        <p>
                            L'unica differenza  che cambia la <i>costante di gravit</i>:
                        </p>
                        <p>
                            <Latex>{r`g_{luna} = 1.62 \frac{m}{s^2}`}</Latex>
                        </p>
                        <p>
                            <Latex>{r`g_{marte} = 3.71 \frac{m}{s^2}`}</Latex>
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Forze di contatto
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Normale
                        </h3>
                        <p>
                            Si oppone alle forze applicate alla superficie di contatto.
                        </p>
                        <p>
                            Un libro appoggiato su un tavolo ha la <b>forza di gravit</b> che lo attira verso il terreno e la <b>forza normale</b> che lo trattiene dal cadere. 
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Attrito statico
                        </h3>
                        <p>
                            Impedisce a un corpo di muoversi se non viene spinto da una forza che supera una certa soglia:
                        </p>
                        <p>
                            <Latex>{r`\left | \vec{F} \right | \leq \mu_{s} \left | \vec{F}_{normale} \right |`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Attrito dinamico
                        </h3>
                        <p>
                            Rallenta i corpi che si stanno muovendo finch essi non si fermano:
                        </p>
                        <p>
                            <Latex>{r`\left | \vec{F} \right | \leq \mu_{d} \left | \vec{F}_{normale} \right |`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Tensione
                        </h3>
                        <p>
                            E' forza trasmessa tra due estremi di una fune.
                        </p>
                        <p>
                            Pu essere redirezionata per mezzo di carrucole.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Elastica
                        </h3>
                        <p>
                            Una molla cerca sempre di tornare alla sua posizione indeformata con forza:
                        </p>
                        <p>
                            <Latex>{r`F = -k x`}</Latex>
                        </p>
                        <p>
                            (E' negativa perch la forza  opposta a quella applicata per deformarla.)
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Cinematica
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Spostamento
                        </h3>
                        <p>
                             un vettore che indica la posizione di un corpo rispetto a un'origine.
                        </p>
                        <p>
                            <Latex>{r`\Delta \vec{s} = \vec{s}(fine) - \vec{s}(inizio)`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Velocit
                        </h3>
                        <p>
                             un vettore che misura la variazione di posizione nel tempo.
                        </p>
                        <p>
                            <Latex>{r`\vec{v} = \frac{\Delta \vec{s}}{\Delta t}`}</Latex>
                        </p>
                        <p>
                            Se si considera un intervallo di tempo infinitesimale si dice <i>velocit istantanea</i>:
                        </p>
                        <p>
                            <Latex>{r`\vec{v} = \lim_{\Delta t \to 0} \frac{\Delta \vec{s}}{\Delta t} = \frac{d \vec{s}}{dt}`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Accelerazione
                        </h3>
                        <p>
                             un vettore che misura la variazione di velocit nel tempo.
                        </p>
                        <p>
                            <Latex>{r`\vec{a} = \frac{\Delta \vec{v}}{\Delta t}`}</Latex>
                        </p>
                        <p>
                            Se si considera un intervallo di tempo infinitesimale si dice <i>accelerazione istantanea</i>:
                        </p>
                        <p>
                            <Latex>{r`\vec{a} = \lim_{\Delta v \to 0} \frac{\Delta \vec{v}}{\Delta t} = \frac{d \vec{v}}{d t} = \frac{d^2 \vec{s}}{d t^2}`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Quantit di moto <small>(momento lineare)</small>
                        </h3>
                        <p>
                            La quantit di moto  una propriet vettoriale dei corpi:
                        </p>
                        <p>
                            <Latex>{r`\vec{p} = m \vec{v}`}</Latex>
                        </p>
                        <p>
                            Se la forza risultante  nulla, la quantit di moto non cambia.
                        </p>
                        <p>
                            <Latex>{r`\Sigma \vec{F} = 0 \Longleftrightarrow \Delta \vec{p} = 0`}</Latex>
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Moto rettilineo uniforme
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Spostamento
                        </h3>
                        <p>
                            La <i>legge oraria</i> :
                        </p>
                        <p>
                            <Latex>{r`s(t) = v \cdot \Delta t + s(0)`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Velocit
                        </h3>
                        <p>
                             costante:
                        </p>
                        <p>
                            <Latex>{r`v(t) = k`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Accelerazione
                        </h3>
                        <p>
                            La velocit non varia:
                        </p>
                        <p>
                            <Latex>{r`a(t) = 0`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Forze
                        </h3>
                        <p>
                            Si applica la prima legge di Newton:
                        </p>
                        <p>
                            <Latex>f(t) = 0</Latex>
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Moto rettilineo uniformemente accelerato
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Spostamento
                        </h3>
                        <p>
                            La <i>legge oraria</i> :
                        </p>
                        <p>
                            <Latex>{r`s(t) = \frac{1}{2} a \cdot (\Delta t)^2 + v(0) \cdot (\Delta t) + s(0)`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Velocit
                        </h3>
                        <p>
                             una retta:
                        </p>
                        <p>
                            <Latex>{r`v(t) = a \Delta t + v(0)`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Accelerazione
                        </h3>
                        <p>
                             costante:
                        </p>
                        <p>
                            <Latex>{r`a(t) = k`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Forze
                        </h3>
                        <p>
                            Si applica la prima legge di Newton:
                        </p>
                        <p>
                            <Latex>f(t) = m a</Latex>
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Moto armonico semplice
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Ampiezza
                        </h3>
                        <p>
                            E' la distanza dal centro massima che raggiunge il corpo.
                        </p>
                        <p>
                            (L'ampiezza di una sinusoide.)
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Velocit angolare
                        </h3>
                        <p>
                            Indica quanto in fretta cambia la posizione del corpo. 
                        </p>
                        <p>
                            Dipende dal periodo:
                        </p>
                        <p>
                            <Latex>{r`\omega = \frac{2 \pi}{T}`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Spostamento
                        </h3>
                        <p>
                            E' una sinusoide:
                        </p>
                        <p>
                            <Latex>{r`s(t) = A \sin (\omega \cdot t + \phi)`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Velocit
                        </h3>
                        <p>
                            E' la sinusoide dello spostamento, sfasata di <Latex>{r`\frac{\pi}{2}`}</Latex>:
                        </p>
                        <p>
                            <Latex>{r`v(t) = A \sin (\omega \cdot t + \phi + \frac{\pi}{2})`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Accelerazione
                        </h3>
                        <p>
                            E' la sinusoide della velocit, sfasata di <Latex>{r`\pi`}</Latex>:
                        </p>
                        <p>
                            <Latex>{r`a(t) = A \sin (\omega \cdot t + \phi + \pi)`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Forze
                        </h3>
                        <p>
                            Si applica la prima legge di Newton:
                        </p>
                        <p>
                            <Latex>f(t) = m a</Latex>
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Moti composti
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Moto parabolico
                        </h3>
                        <p>
                            Il moto parabolico  dato sommando un moto rettilineo uniforme sull'asse orizzontale e un moto rettilineo uniformemente accelerato sull'asse verticale.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Moto circolare uniforme
                        </h3>
                        <p>
                            Il moto parabolico  dato sommando due moti armonici semplici: uno sull'asse X, e l'altro, sfasato di <Latex>{r`\frac{\pi}{2}`}</Latex>, sull'asse Y.
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Moto circolare uniforme
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Velocit angolare
                        </h3>
                        <p>
                            Quanto cambia la fase nel tempo.
                        </p>
                        <p>
                            <Latex>{r`\omega = \frac{2 \pi}{T}`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Fase
                        </h3>
                        <p>
                            E' l'angolo percorso dal corpo rispetto alla posizione iniziale.
                        </p>
                        <p>
                            Si indica con <Latex>{r`\phi`}</Latex>, e generalmente si usa in radianti.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Velocit
                        </h3>
                        <p>
                            Si applicano le formule per la circonferenza:
                        </p>
                        <p>
                            <Latex>{r`v = \frac{\Delta s}{t} = \frac{2 \pi \cdot r}{T} = \omega r`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Accelerazione
                        </h3>
                        <p>
                            Il corpo ha sempre un accelerazione verso il centro che gli impedisce di abbandonare il moto: 
                        </p>
                        <p>
                            <Latex>{r`a = \frac{v^2}{r} = r \cdot \omega^2 = v \cdot \omega`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Forza centripeta
                        </h3>
                        <p>
                             verso il centro e si calcola con:
                        </p>
                        <p>
                            <Latex>{r`F = m \cdot a`}</Latex>
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Lavoro ed energia
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Lavoro
                        </h3>
                        <p>
                            E' compiuto da una forza che sposta un corpo.
                        </p>
                        <p>
                            <Latex>{r`W = \vec{F} \cdot \vec{s} = F \cdot \Delta s \cdot cos(\alpha )`}</Latex>
                        </p>
                        <p>
                            (Se la forza non  parallela allo spostamento, il prodotto scalare ci fa considerare solo la componente parallela.)
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Energia cinetica
                        </h3>
                        <p>
                            Un corpo ha energia cinetica in ogni momento uguale a:
                        </p>
                        <p>
                            <Latex>{r`E_c = \frac{1}{2} m v^2`}</Latex>
                        </p>
                        <p>
                            Se una forza effettua lavoro su un corpo, cambia la sua energia cinetica pari al lavoro effettuato:
                        </p>
                        <p>
                            <Latex>{r`\Delta E_c = W`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Energia potenziale gravitazionale
                        </h3>
                        <p>
                            Un corpo ha energia potenziale in ogni momento pari a: 
                        </p>
                        <p>
                            <Latex>{r`E_{p_g} = m \cdot g \cdot h`}</Latex>
                        </p>
                        <p>
                            (Con <Latex>h</Latex> uguale a un altezza scelta come punto di riferimento.)
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Energia potenziale elastica
                        </h3>
                        <p>
                            Una molla ha sempre energia potenziale elastica pari a:
                        </p>
                        <p>
                            <Latex>{r`E_{p_e} = \frac{1}{2} k x^2`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Forze conservative
                        </h3>
                        <p>
                            Sono conservative le forze per le quali il lavoro compiuto non dipende dal percorso seguito per andare dalla partenza all'arrivo.
                        </p>
                        <p>
                            Ad esempio,  conservativa la <i>forza di gravit</i>, ma <b>non</b>  conservativa la forza di attrito.
                        </p>
                        <p>
                            Se in un sistema ci sono solo forze conservative, allora l'energia meccanica totale si conserva:
                        </p>
                        <p>
                            <Latex>{r`E = E_k + E_p`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Potenza
                        </h3>
                        <p>
                             la velocit di trasferimento di energia:
                        </p>
                        <p>
                            <Latex>{r`P = \frac{\Delta E}{\Delta t}`}</Latex>
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Elettrostatica
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Carica elettrica
                        </h3>
                        <p>
                             una propriet dei corpi che pu essere <Plus>positiva</Plus> o <Minus>negativa</Minus>.
                        </p>
                        <p>
                            Si conserva: in un sistema chiuso la carica totale  costante.
                        </p>
                        <p>
                            Esiste un'unit elementare: <Latex>{r`C_{elettrone} = 1.602 \cdot 10^{-19}`}</Latex>.
                        </p>
                        <p>
                            Cariche <Plus>opp</Plus><Minus>oste</Minus> si attraggono; cariche <Plus>uguali</Plus> si respingono.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Conduttori e isolanti
                        </h3>
                        <p>
                            Pi <a href="https://it.wikipedia.org/wiki/Ione">ioni</a> ha un corpo, meglio la carica si muove attraverso di esso.
                        </p>
                        <p>
                            I corpi in cui la carica si muove bene sono <i>conduttori</i>, mentre quelli in cui si muove difficilmente sono <i>isolanti</i>.
                        </p>
                        <p>
                            Il corpo umano  un buon conduttore.
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Polarizzazione
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Polarizzazione
                        </h3>
                        <p>
                            E' possibile polarizzare un corpo per accumulare la carica di un segno in una certa zona.
                        </p>
                    </Panel>
                </Split>
                <Split>
                    <Panel>
                        <h3>
                            Messa a terra
                        </h3>
                        <p>
                            Se un corpo conduttore  in contatto con la Terra, le cariche su di esso saranno <i>equilibrate</i> e il corpo diventer elettricamente neutro (con stesso numero di <Plus>cariche positive</Plus> e <Minus>negative</Minus> all'interno).
                        </p>
                    </Panel>
                </Split>
                <Split>
                    <Panel>
                        <h3>
                            Polarizzazione per strofinio
                        </h3>
                        <p>
                            Strofinando tra loro due corpi isolanti, essi si <i>polarizzeranno per strofinio</i>.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Polarizzazione per contatto
                        </h3>
                        <p>
                            Toccando un conduttore con un corpo carico, il conduttore potr <i>polarizzarsi per contatto</i>.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Polarizzazione per induzione
                        </h3>
                        <p>
                            Se un corpo conduttore ha cariche "esterne" di un <Plus>certo segno</Plus> vicino, esso avr tutte le cariche del <Minus>segno opposto</Minus> in equilibrio vicino alle cariche esterne, e tutte le cariche dello <Plus>stesso segno</Plus> pi lontano possibile da esse.
                        </p>
                        <p>
                            Mettendo a terra il conduttore, nuove cariche del <Minus>segno opposto</Minus> saranno attratte all'interno del corpo per equilibrare le cariche che si sono allontanate.
                        </p>
                        <p>
                            Staccando il conduttore da terra e rimuovendo le cariche esterne, esso si ritrover <Minus>caricato del segno opposto</Minus> rispetto alle cariche esterne.
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Forza elettrica
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Legge di Coulomb
                        </h3>
                        <p>
                            Due corpi carichi si attraggono tra loro con forza: 
                        </p>
                        <p>
                            <Latex>{r`\left | \vec{F}_{elettrica} \right | = \frac{-k \cdot q_1 \cdot q_2}{s^2}`}</Latex>
                        </p>
                        <p>
                            <Latex>{r`k`}</Latex>  la <i>costante di Coulomb</i>, e vale <Latex>{r`k = 8.99 \cdot 10^9 \frac{N \cdot m^2}{C^2}`}</Latex>.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Permeabilit dello spazio vuoto
                        </h3>
                        <p>
                            La costante <Latex>{r`k`}</Latex>  in realt dipendente da un altra costante, <Latex>{r`\epsilon_0`}</Latex>, la <i>permeabilit del vuoto</i>.
                        </p>
                        <p>
                            <Latex>{r`k = \frac{1}{4 \pi \cdot \epsilon_0}`}</Latex>
                        </p>
                        <p>
                            <Latex>{r`\left | \vec{F}_{elettrica} \right | = \frac{q_1 \cdot q_2}{4 \pi \cdot \epsilon_0 \cdot s^2}`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Campo elettrico
                        </h3>
                        <p>
                            Misura che forza viene applicata in ogni punto su una carica unitaria:
                        </p>
                        <p>
                            <Latex>{r`\vec{E} = \frac{\vec{F}_{elettrica}}{q} = \frac{-k \cdot q}{s^2}`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Flusso elettrico
                        </h3>
                        <p>
                             la differenza tra "quanto" campo elettrico <Plus>entra</Plus> e quanto campo elettrico <Minus>esce</Minus> da una certa area.
                        </p>
                        <p>
                             proporzionale alla intensit del campo, alla dimensione dell'area scelta e a come l'area  disposta rispetto alla direzione del campo.
                        </p>
                        <p>
                            Se il campo elettrico  uniforme, se ne pu calcolare facilmente il valore:
                        </p>
                        <p>
                            <Latex>{r`\Phi_E = \vec{E} \cdot \vec{S} = E \cdot S \cdot \cos(\phi)`}</Latex>
                        </p>
                        <p>    
                            (dove <Latex>{r`\vec{S}`}</Latex>  il vettore dell'area che abbiamo scelto.)
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Legge di Gauss
                        </h3>
                        <p>
                            Il flusso elettrico uscente da una superficie  proporzionale alla carica presente al suo interno.
                        </p>
                        <p>
                            <Latex>{r`\Phi_E = \frac{q_{interna}}{\epsilon_0}`}</Latex>
                        </p>
                        <p>
                            E' equivalente alla Legge di Coulomb.
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Energia elettrica
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Energia potenziale elettrica
                        </h3>
                        <p>
                            Un corpo carico vicino ad altre cariche possiede un'<i>energia potenziale elettrica</i> <Latex>{r`U_e`}</Latex>.
                        </p>    
                    </Panel>
                </Split>
                <h2>
                    Circuiti elettrici
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Potenziale elettrico (Tensione)
                        </h3>
                        <p>
                             il valore dell'energia potenziale elettrica per una carica unitaria.
                        </p>
                        <p>
                            <Latex>{r`V = \frac{E_{elettrica}}{q}`}</Latex>
                        </p>
                        <p>
                            La sua unit di misura  il Volt (<Latex>{r`V`}</Latex>).
                        </p>                        
                        <p>
                            In una batteria  detto <i>forza elettromotrice</i>, e corrisponde al lavoro compiuto da una batteria ideale per spostare una carica unitaria tra i due poli.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Corrente elettrica (Intensit)
                        </h3>
                        <p>
                            Quanta carica passa attraverso un'area (perpendicolare al flusso) nel tempo.
                        </p>
                        <p>
                            <Latex>{r`I = \frac{\Delta q}{\Delta t}`}</Latex>
                        </p>
                        <p>
                            Fintanto che c' differenza di potenziale, ci sar anche intensit non nulla.
                        </p>
                        <p>
                            La sua unit di misura  l'Ampere (<Latex>{r`A`}</Latex>).
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Corrente continua (<abbr title="Direct Current">DC</abbr>)
                        </h3>
                        <p>
                            Quando in un circuito la direzione della corrente  costante.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Corrente alternata (<abbr title="Alternate Current">AC</abbr>)
                        </h3>
                        <p>
                            Quando in un circuito la direzione della corrente si alterna periodicamente.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Potenza elettrica
                        </h3>
                        <p>
                            Possiamo calcolare la potenza di un circuito:
                        </p>
                        <p>
                            <Latex>{r`P = \frac{\Delta U}{\Delta t} = I \cdot \Delta V = I^2 \cdot R = \frac{(\Delta V)^2}{R}`}</Latex>
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Elementi di un circuito
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Resistore
                        </h3>
                        <p>
                            Riduce l'intensit di corrente, e converte parte del potenziale in calore.
                        </p>
                        <p>
                            Il potenziale utilizzato  pari a:
                        </p>
                        <p>
                            <Latex>{r`V = R \cdot I`}</Latex>
                        </p>
                        <p>
                            Dove <Latex>{r`R`}</Latex>  una costante detta <i>resistenza</i> con unit di misura Ohm (<Latex>{r`\Omega`}</Latex>).
                        </p>
                        <p>
                            La resistenza di un conduttore vale:
                        </p>
                        <p>
                            <Latex>{r`R = \rho \frac{L_{unghezza}}{A_{rea}}`}</Latex>
                        </p>
                        <p>
                            <Latex>{r`\rho`}</Latex>  la <i>resistivit</i> del materiale, e varia in base alla temperatura:
                        </p>
                        <p>
                            <Latex>{r`\rho = \rho_0 (1 + \alpha(T - T_0))`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Condensatore
                        </h3>
                        <p>
                            Immagazzina potenziale elettrico, permettendo di riutilizzarla in seguito.
                        </p>
                        <p>
                            Per farlo, cattura cariche <Plus>positive</Plus> e <Minus>negative</Minus> sulle sue due armature; perch questo avvenga, deve essere compiuto lavoro.
                        </p>
                        <p>
                            Ha una <b>capacit</b> caratteristica, che in un condensatore a facce piane parallele :
                        </p>
                        <p>
                            <Latex>{r`C = \frac{q_{massima}}{\Delta V}`}</Latex>
                        </p>
                        <p>
                            Condensatori di capacit maggiore immagazzinano pi potenziale con meno carica.
                        </p>
                        <p>
                            La capacit aumenta se viene messo qualcosa tra le armature:
                        </p>
                        <p>
                            <Latex>{r`C_{nuova} = \kappa \cdot \frac{\epsilon_0 \cdot A}{s}`}</Latex>
                        </p>
                        <p>
                            Dove <Latex>{r`\kappa`}</Latex>  la <i>costante dielettrica relativa</i> del materiale inserito, <Latex>{r`A`}</Latex> l'area di una armatura e <Latex>{r`d`}</Latex> la distanza tra le due armature.
                        </p>
                        <p>
                            Se il campo elettrico creatosi tra le due armature supera la <i>rigidit dielettrica</i> del condensatore, la carica immagazzinata viene persa e ha luogo un <i>breakdown</i>.
                        </p>
                        <p>
                            La sua unit di misura  il Farad (<Latex>{r`Fa`}</Latex>)
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Amperometro
                        </h3>
                        <p>
                            Misura la corrente elettrica se messo in serie.
                        </p>
                        <p>
                            (Funzionamento: ha una resistenza interna bassisima in modo da non influire significativamente sulla corrente.)
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Voltmetro
                        </h3>
                        <p>
                            Misura la differenza di potenziale se messo in parallelo.
                        </p>
                        <p>
                            (Funzionamento: ha una resistenza altissima in modo da non influire significativamente sulla tensione.)
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Principi di Kirchhoff
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Legge dei nodi
                        </h3>
                        <p>
                            Per nodo si intende un qualsiasi punto del circuito.
                        </p>
                        <p>
                            Da un nodo entra ed esce la stessa corrente.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Legge delle maglie
                        </h3>
                        <p>
                            Per maglia si intende un qualsiasi percorso chiuso all'interno del circuito.
                        </p>
                        <p>
                            In una maglia chiusa, la somma delle differenze di potenziale  0.
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Serie e Parallelo
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Circuito in serie
                        </h3>
                        <p>
                            Pi parti di circuito sono <i>in serie</i> se sono consecutive e senza biforcazioni.
                        </p>
                        <p>
                            Parti di circuito in serie sono attraversate dalla stessa corrente.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Circuito in parallelo
                        </h3>
                        <p>
                            Pi parti di circuito sono <i>in parallelo</i> tra loro se hanno lo stesso punto di partenza e lo stesso punto di arrivo. 
                        </p>
                        <p>
                            Parti di circuito in parallelo hanno la stessa differenza di potenziale.
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Resistenze equivalenti
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Circuiti in serie
                        </h3>
                        <p>
                            Nei circuiti in serie, tutte le resistenze possono essere sostituite con una equivalente dal valore della somma di tutte le quelle sostituite:
                        </p>
                        <p>
                            <Latex>{r`R_{serie} = \sum_{i=1}^{n} R_i`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Circuiti in parallelo
                        </h3>
                        <p>
                            Nei circuiti in parallelo, tutte le resistenze possono essere sostituite con una equivalente dal valore di:
                        </p>
                        <p>
                            <Latex>{r`R_{parallelo} = \frac{1}{\sum_{i=1}^{n} \frac{1}{R_i}}`}</Latex>
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Condensatori equivalenti
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Circuiti in serie
                        </h3>
                        <p>
                            Nei circuiti in serie, tutte i condensatori possono essere sostituiti con uno equivalente dal valore di:
                        </p>
                        <p>
                            <Latex>{r`C_{serie} = \frac{1}{\sum_{i=1}^{n} \frac{1}{C_i}}`}</Latex>
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Circuiti in parallelo
                        </h3>
                        <p>
                            Nei circuiti in parallelo, tutte i condensatori possono essere sostituite con uno equivalente dal valore della somma della capacit di tutti quelli sostituiti:
                        </p>
                        <p>
                            <Latex>{r`C_{parallelo} = \sum_{i=1}^{n} C_n`}</Latex>
                        </p>
                    </Panel>
                </Split>
                <h2>
                    Magnetismo
                </h2>
                <Split>
                    <Panel>
                        <h3>
                            Campo magnetico
                        </h3>
                        <p>
                            Come un campo elettrico, ma per i magneti.
                        </p>
                        <p>
                            <Latex>B</Latex>
                        </p>
                        <p>
                            La sua unit di misura  il Tesla (<Latex>T</Latex>).
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Flusso magnetico
                        </h3>
                        <p>
                            Come il flusso elettrico, ma per campi magnetici.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Legge di Gauss per il magnetismo
                        </h3>
                        <p>
                            Il flusso magnetico attraverso qualsiasi superficie chiusa  sempre nullo.
                        </p>
                    </Panel>
                </Split>
                <Split>
                    <Panel>
                        <h3>
                            Forza magnetica su carica puntiforme
                        </h3>
                        <p>
                            <i>Anche detta <b>forza di Lorentz</b>.</i>
                        </p>
                        <p>
                            I campi magnetici applicano una forza sulle cariche vicine:
                        </p>
                        <p>
                            <Latex>{r`\vec{F}_{magnetica} = q \cdot (\vec{v} \times \vec{B})`}</Latex>
                        </p>
                        <p>
                            Dove <Latex>{r`\vec{B}`}</Latex>  l'intensit del campo magnetico e <Latex>{r`\vec{v}`}</Latex> la velocit della carica considerata.
                        </p>
                        <p>
                            Si ha una forza massima se la velocit  perpendicolare al campo magnetico.
                        </p>
                        <p>
                            In un campo magnetico uniforme, una velocit perpendicolare al campo porta alla creazione di un moto circolare uniforme.
                        </p>
                    </Panel>
                    <Panel>
                        <h3>
                            Forza magnetica in un filo
                        </h3>
                        <p>
                            I campi magnetici influenzano anche le cariche presenti in un conduttore:
                        </p>
                        <p>
                            <Latex>{r`\vec{F} = I \cdot (\vec{L} \times \vec{B})`}</Latex>
                        </p>
                    </Panel>
                </Split>
            </div>
        )
	}
}


